body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: relative;
  margin-bottom: 0;
}

.Header {
  width: 100%;
  background-color: #2A77AE;
  color: yellow;
}

.navbar {
  height: 40px;
  max-width: 1130px;
  padding-top: 10px;
  margin-left: auto;
  margin-right: auto;
}

.navbar-item {
  margin-left: 20px;
  margin-right: 20px;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  max-width: 1130px;
  height: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
  margin-left: auto;
  margin-right: auto;
}

.jumbotron {
  padding-top: 10px;
  width: 100%;
  height: 100px;
  font-size: 3rem;
  color: #2A77AE;
  background-color: rgb(221, 209, 102);
}

.card {
  border-radius: 2px;
  height: 150px;
  margin: 10px;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  border:#6CADDC;
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}

.footer {
  min-height: 40px;
}

.footer,
.footertext {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background-color: #2A77AE;
  color: yellow;
}
